import React from 'react';
import { ArrowRightAlt } from "@material-ui/icons";
import { Link } from "react-router-dom";
import {useSelector} from 'react-redux';

export default function HomeDescription() {
  const { data: { pageData } } = useSelector(s => s);
  const { home } = pageData;
  if (!home) return null;
  const { intro, description, profile } = home;
  
  return (
    <div className="flex lg:flex-row lg:justify-start flex-col-reverse items-center">
      <div className="w-3/4 md:w-1/2 my-2">
        <h1 className="text-5xl font-semibold">{intro}</h1>
        <h3 className="text-2xl my-4">{profile}</h3>
        <p className="text-xl">{description}</p>
        <button className="py-4 px-6 font-semibold text-xl mt-10 bg-purple-500 text-white rounded-xl hover:bg-purple-600 shadow focus:outline-none">
          <Link to="/contact">
            Contact Me <ArrowRightAlt />
          </Link>
        </button>
      </div>
      <div className="mb-10">
        <img
          src={"https://iamsvkr.github.io/shivam1.jpg"}
          alt="Profile image"
          className="rounded-full lg:h-96 h-72"
        />
      </div>
    </div>
  );
}
