import { Link } from "react-router-dom";
import { Brightness2, WbSunny } from "@material-ui/icons";
// import ThemeContext from "../store/theme-context";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { themeActions } from "../store";

export default function Nav() {
  // const { nightTheme, setNightTheme } = useContext(ThemeContext);

  const dispatch = useDispatch();
  const { theme: { nightMode } } = useSelector(s => s);

  return (
    <nav className="flex justify-around py-6 text-lg">
      <div>Shivam</div>
      <ul className="flex">
        <li className="px-3">
          <Link to="/">Home</Link>
        </li>
        <li className="px-3">
          <Link to="/about">About</Link>
        </li>
        <li className="px-3">
          <Link to="/skills">Skills</Link>
        </li>
        <li className="px-3">
          <Link to="/contact">Contactme</Link>
        </li>
        <li onClick={() => dispatch(themeActions.toggleTheme())} className="px-3">
          {nightMode ? <Brightness2 /> : <WbSunny />}
        </li>
      </ul>
    </nav>
  );
}
