import { configureStore, createSlice } from "@reduxjs/toolkit";

const themeSlice = createSlice({
  name: "theme",
  initialState: {
    nightMode: false,
  },
  reducers: {
    toggleTheme(state) {
      state.nightMode = !state.nightMode;
    },
  },
});

const dataSlice = createSlice({
  name: "data",
  initialState: {
    pageData: [],
  },
  reducers: {
    setPageData(state, actions) {
      state.pageData = actions.payload;
    },
  },
});

export const fetchPageData = () => async (dispatch) => {
  try {
    const response = await fetch("https://iamsvkr.github.io/data.json");
    const data = await response.json();
    dispatch(dataSlice.actions.setPageData(data));
  } catch (error) {
    console.log(error);
  }
};

const store = configureStore({
  reducer: { theme: themeSlice.reducer, data: dataSlice.reducer },
});

export const themeActions = themeSlice.actions;
export const dataActions = dataSlice.actions;

export default store;
